import axios from 'axios';
import config from 'app/auth/services/administrator/systemAdmin/config';

const getSystemAdmin = (paramObj) => {
  return new Promise((resolve, reject) => {
    axios
      .get(config.getSystemAdmin, {
        params: paramObj,
      })
      .then((response) => {
        if (response.data.code === 200) {
          const data = response.data.response;
          resolve(data);
        } else {
          reject(response);
        }
      })
      .catch((error) => {
        reject(error);
      });
  });
};

const getSystetmAdminDetail = (userId) => {
  return new Promise((resolve, reject) => {
    axios
      .get(config.getSystemAdminDetail.replace(':userId', userId))
      .then((response) => {
        if (response.data.code === 200) {
          const data = response.data.response;
          resolve(data);
        } else {
          reject(response);
        }
      })
      .catch((error) => {
        reject(error);
      });
  });
};

const updateSystemAdmin = (systemAdminObj) => {
  return new Promise((resolve, reject) => {
    axios
      .put(config.addOrUpdateSystemAdmin, systemAdminObj)
      .then((response) => {
        if (response.data.code === 200) {
          const data = response.data.response;
          resolve(data);
        } else {
          reject(response);
        }
      })
      .catch((error) => {
        reject(error);
      });
  });
};

export { getSystemAdmin, getSystetmAdminDetail, updateSystemAdmin };
