import { useLocation, useNavigate } from 'react-router-dom';
import 'react-datepicker/dist/react-datepicker.css';
import { useRef } from 'react';
import 'assets/css/batteriesAddOrUpdate.css';
import { updateSystemAdmin } from 'app/auth/services/administrator/systemAdmin';
import { useDispatch } from 'react-redux';
import { checkPhoneNum, checkEmail } from 'app/utils/validation';
import { DetailTitleBar } from 'app/theme-layouts/mainLayout/components';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';
import { open, close } from 'app/store/common/loadingWrap';
import classnames from 'classnames';
import { showMessage } from '../../../store/fuse/messageSlice';

function AddOrUpdate() {
  const dispatch = useDispatch();

  const deleteBtnClick = () => {
    dispatch(showMessage({ message: '삭제 미연동', variant: 'success' }));
  };

  const saveBtnClick = () => {
    confirmAlert({
      title: `시스템 관리자 정보를 ${param.detailLink[0].title} 하시겠습니까?`,
      buttons: [
        {
          label: '예',
          onClick: () => {
            if (!checkEmail(userId.current.value)) {
              dispatch(
                showMessage({ message: '사용자ID 이메일 형식을 확인해주세요', variant: 'error' })
              );
              return;
            }
            if (!checkPhoneNum(phone.current.value)) {
              dispatch(showMessage({ message: '연락처 형식을 확인해주세요', variant: 'error' }));
              return;
            }
            const systemAdminObj = {};
            systemAdminObj.userId = userId.current.value;
            systemAdminObj.username = username.current.value;
            systemAdminObj.phone = phone.current.value;
            systemAdminObj.userDesc = userDesc.current.value;
            systemAdminObj.useYn = useYn.current.value;
            dispatch(open());
            switch (param.detailLink[0].title) {
              case '추가':
                dispatch(showMessage({ message: '추가 미연동', variant: 'success' }));
                break;
              case '수정':
                updateSystemAdmin(systemAdminObj)
                  .then((data) => {
                    dispatch(
                      showMessage({
                        message: '시스템 관리자 정보가 수정되었습니다',
                        variant: 'success',
                      })
                    );
                  })
                  .catch((error) => {
                    dispatch(
                      showMessage({ message: '시스템 관리자 정보 수정 실패', variant: 'error' })
                    );
                  })
                  .finally(() => {
                    dispatch(close());
                  });
                break;
              default:
                break;
            }
          },
        },
        {
          label: '취소',
        },
      ],
    });
  };

  const userId = useRef();
  const username = useRef();
  const phone = useRef();
  const userDesc = useRef();
  const useYn = useRef();

  const navigate = useNavigate();
  const location = useLocation();
  const param = location.state;

  return (
    <div className="section__grid__wrap content batteries__add__update system__admin">
      <DetailTitleBar flag={param.detailLink[0].title} saveBtnClick={saveBtnClick} />
      {/* input__wrap */}
      <section className="input__wrap">
        <div className="section__inner">
          <ul className="selector__wrap">
            <li>
              <dl>
                <dt>사용자ID</dt>
                <dd>
                  <input
                    defaultValue={param.userId}
                    type="text"
                    className={classnames('custom__input', [
                      { readonly: param.detailLink[0].title === '수정' },
                    ])}
                    readOnly={param.detailLink[0].title === '수정'}
                    ref={userId}
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>사용자명</dt>
                <dd>
                  <input
                    defaultValue={param.username}
                    type="text"
                    className="custom__input"
                    ref={username}
                  />
                </dd>
              </dl>
            </li>
            <li>
              <dl>
                <dt>연락처</dt>
                <dd>
                  <input
                    defaultValue={param.phone}
                    type="text"
                    className="custom__input"
                    ref={phone}
                  />
                </dd>
              </dl>
            </li>
            <li className="w100">
              <dl>
                <dt>설명</dt>
                <dd>
                  <textarea
                    defaultValue={param.userDesc}
                    className="custom__input"
                    ref={userDesc}
                  />
                </dd>
              </dl>
            </li>
            <li className="w50">
              <dl>
                <dt>사용여부</dt>
                <dd>
                  <select defaultValue={param.userYn} className="custom__select" ref={useYn}>
                    <option value="1">사용</option>
                    <option value="0">미사용</option>
                  </select>
                </dd>
              </dl>
            </li>
          </ul>
        </div>
      </section>
      {/* // input__wrap */}
    </div>
  );
}

export default AddOrUpdate;
